<template>
  <div class="detail-table">
    <el-table :data="detailList" border style="width:98.2%" @row-click="rowCurrentChange">
      <el-table-column v-for="(item,index) in tableTitleList"  :key="index" :width="item.rowName=='headOfficeOrderNum'||item.rowName=='branchOfficeOrderNum'?90:''" :prop="item.rowName" :label="item.rowDesc" align="center" v-if="item.visible" :show-overflow-tooltip="true"></el-table-column>
      <!--<el-table-column prop="materialCategoryName" label="" align="center" :show-overflow-tooltip="true"></el-table-column>-->
      <!--<el-table-column prop="materialName" label="" align="center" :show-overflow-tooltip="true"></el-table-column>-->
      <!--<el-table-column prop="materialCode" label="" align="center" :show-overflow-tooltip="true"></el-table-column>-->
      <!--<el-table-column prop="modelNum" label="" align="center" :show-overflow-tooltip="true"></el-table-column>-->
      <!--<el-table-column prop="specification" label="" align="center" :show-overflow-tooltip="true"></el-table-column>-->
      <!--&lt;!&ndash;<el-table-column prop="ownerName" label="" align="center" :show-overflow-tooltip="true"></el-table-column>&ndash;&gt;-->
      <!--&lt;!&ndash;<el-table-column prop="headOfficeOrderNum" label="" width="97" align="center" :show-overflow-tooltip="true"></el-table-column>&ndash;&gt;-->
      <!--&lt;!&ndash;<el-table-column prop="branchOfficeOrderNum" label="" width="90" align="center" :show-overflow-tooltip="true"></el-table-column>&ndash;&gt;-->
      <!--&lt;!&ndash;<el-table-column prop="storeArea" label="" align="center" :show-overflow-tooltip="true"></el-table-column>&ndash;&gt;-->
      <!--&lt;!&ndash;<el-table-column prop="goodsAllocation" label="" align="center" :show-overflow-tooltip="true"></el-table-column>&ndash;&gt;-->
      <!--<el-table-column prop="stockOutAmount" label="" align="center" :show-overflow-tooltip="true"></el-table-column>-->
      <!--<el-table-column prop="stockAmount" label="" align="center" :show-overflow-tooltip="true"></el-table-column>-->
      <!--<el-table-column prop="measureUnit" label="" align="center" :show-overflow-tooltip="true"></el-table-column>-->
    </el-table>
    <el-dialog title="" :visible.sync="dialogVisible" top="20vh" v-if="dialogVisible">
      <sold-out-information  :tableData="downShelfList"></sold-out-information>
    </el-dialog>
    <span class="right-content-body-little-title">
            
    </span>
    <el-table :data="reviewList" border style="width:98.2%">
      <el-table-column prop="reviewDate" label="" align="center" :show-overflow-tooltip="true"></el-table-column>
      <el-table-column prop="reviewerName" label="" align="center" :show-overflow-tooltip="true"></el-table-column>
      <el-table-column prop="reveiwResultKey" label="" align="center" :show-overflow-tooltip="true">
        <template slot-scope="scope">
          {{scope.row.reveiwResultKey == 1?"":""}}
        </template>
      </el-table-column>
      <el-table-column prop="refuseReason" label="" align="center" :show-overflow-tooltip="true"></el-table-column>
    </el-table>
  </div>
</template>
<script>
  import SoldOutInformationAlert from "./SoldOutInformationAlert.vue"
  export default{
    name:"Info",
    mounted(){
      this.id=this.$route.params.id;
      this.getInfo();
    },
    data(){
      return{
        id:"",
        tableTitleList:[],
        dialogVisible:false,
        detailList:[],//
        reviewList:[],//
        downShelfList:[],//
      }
    },
    methods:{
      getInfo(){
        this.$http.get("/ys-web-wms/putOutStorageInfo/getInfo?id="+this.id).then(function (result) {
          if(result.data.state == 200){
            let data = result.data.data;
            if(data){
              this.detailList = data.detailList?data.detailList:[];
              this.reviewList = data.reviewList?data.reviewList:[];
              let _this = this;
              this.getPutOutStorageTableTittle(data.basicInfo.stockOutTypeKey,function (res) {
                _this.tableTitleList = res;
              })
            }else{
              this.detailList = [];
              this.reviewList = [];
            }
          }else{
            this.$notify.error({
              title:"",
              message:result.data.message
            })
          }
        })
      },
      rowCurrentChange(row, event, column){
        this.$http.get("/ys-web-wms/putOutStorageInfo/getDetailDownShelfList?id="+row.stockOutDetailId).then(function (result) {
          if(result.data.state == 200){
            this.downShelfList = result.data.data?result.data.data:[];
            this.dialogVisible = true;
          }else{
            this.$notify.error({
              title:"",
              message:result.data.message
            })
            this.downShelfList = [];
          }
        })

      }
    },
    components:{
      "sold-out-information":SoldOutInformationAlert
    },
    watch:{
      "$route.params.id":function () {
        this.id = this.$route.params.id
        this.getInfo();
      },
      "$store.state.putOutStorageInfo.stockOutStatusKey":function () {
        this.getInfo();
      }
    }
  }
</script>
<style>
  .detail-table .el-dialog__header{padding: 0px}
  .detail-table .el-dialog__body{padding: 0px}
  /*.detail-table .el-dialog{width: 37%}*/
  .detail-table .el-dialog__headerbtn{  top: 10px;}
</style>



