<template>
  <div>
    <div class="right-topTitle">
      {{$store.state.putOutStorageInfo.stockOutCode}}
    </div>
    <div class="right-content">
      <div class="right-content-body">
        <div style="padding: 5px" v-if="!toSoldOut">
          <el-button size="mini" style="float: right;margin: 0px 10px 10px 10px;" type="primary" @click="soldOut" v-if="$store.state.putOutStorageInfo.stockOutStatusKey == '3' || $store.state.putOutStorageInfo.stockOutStatusKey == '8'"></el-button>
          <el-table :data="stockOutDownShelfList" border style="width: 100%">
            <el-table-column prop="materialCode" label="" align="center" :show-overflow-tooltip="true"></el-table-column>
            <el-table-column prop="materialName" label="" align="center" :show-overflow-tooltip="true"></el-table-column>
            <el-table-column prop="warehouseName" label="" align="center" :show-overflow-tooltip="true"></el-table-column>
            <el-table-column prop="storeAreaCode" label="" align="center" :show-overflow-tooltip="true"></el-table-column>
            <el-table-column prop="goodsAllocationCode" label="" align="center" :show-overflow-tooltip="true"></el-table-column>
            <el-table-column prop="downAmount" label="" align="center" :show-overflow-tooltip="true"></el-table-column>
            <el-table-column prop="stockBarcode" label="" align="center" :show-overflow-tooltip="true"></el-table-column>
            <el-table-column prop="materialOwnerName" label="" align="center" :show-overflow-tooltip="true"></el-table-column>
            <el-table-column prop="headOfficeOrderNum" label="" width="90" align="center" :show-overflow-tooltip="true"></el-table-column>
            <el-table-column prop="branchOfficeOrderNum" label="" width="90" align="center" :show-overflow-tooltip="true"></el-table-column>
            <el-table-column prop="measureUnit" label="" align="center" :show-overflow-tooltip="true"></el-table-column>
            <el-table-column prop="downShelfPersonName" label="" align="center" :show-overflow-tooltip="true"></el-table-column>
            <el-table-column prop="downDate" label="" align="center" :show-overflow-tooltip="true"></el-table-column>
          </el-table>
        </div>
        <div style="padding: 5px" v-else>
          <el-form :model="detailForm" ref="detailForm" :inline-message="true">
            <el-table :data="detailForm.tableData" border style="width: 100%" class="operation-table">
              <el-table-column prop="materialCode" label="" align="center" :show-overflow-tooltip="true"></el-table-column>
              <el-table-column prop="materialName" label="" align="center" :show-overflow-tooltip="true"></el-table-column>
              <!--<el-table-column prop="stockAmount" label="" align="center" :show-overflow-tooltip="true"></el-table-column>-->
              <el-table-column prop="waitDownAmount" label="" align="center" :show-overflow-tooltip="true"></el-table-column>
              <el-table-column prop="actualDownAmount" label="" align="center" :show-overflow-tooltip="true">
                <template slot-scope="scope">
                  <el-form-item label="" :prop="'tableData.'+scope.$index+'.actualDownAmount'" :rules="rules.amount">
                    <el-input size="mini" v-model="scope.row.actualDownAmount"></el-input>
                  </el-form-item>
                </template>
              </el-table-column>
              <el-table-column prop="date" label="" align="center" :show-overflow-tooltip="true">
                <template slot-scope="scope">
                  <el-form-item label="" :prop="'tableData.'+scope.$index+'.goodsAllocationCode'">
                    <el-select v-model="scope.row.goodsAllocationCode" placeholder="" size="mini" @change="goodsAllocationCodeChange(scope.row)">
                      <el-option
                        v-for="item in scope.row.stockDetailList"
                        :key="item.storagebinCode"
                        :label="item.storagebinCode+''+item.stockAmount"
                        :value="item.storagebinCode">
                      </el-option>
                    </el-select>
                  </el-form-item>
                </template>
              </el-table-column>
              <el-table-column prop="stockBarcode" label="" align="center" :show-overflow-tooltip="true">
                <template slot-scope="scope">
                  <el-select v-model="scope.row.stockId" placeholder="" size="mini" @change="barcodeChange(scope.row)">
                    <el-option
                      v-for="item in scope.row.barcodeList"
                      :key="item.stockId"
                      :label="item.stockBarcode+''+item.stockAmount"
                      :value="item.stockId">
                    </el-option>
                  </el-select>
                </template>
              </el-table-column>
              <el-table-column prop="barcodeAmount" label="" align="center" :show-overflow-tooltip="true">
              </el-table-column>
              <el-table-column prop="stockInDate" label="" align="center" :show-overflow-tooltip="true">
              </el-table-column>
              <!--<el-table-column prop="materialOwnerName" label="" align="center" :show-overflow-tooltip="true">-->
              <!--</el-table-column>-->
              <!--<el-table-column prop="headOfficeOrderNum" label="" width="90" align="center" :show-overflow-tooltip="true">-->
              <!--</el-table-column>-->
              <!--<el-table-column prop="branchOfficeOrderNum" label="" width="90" align="center" :show-overflow-tooltip="true">-->
              <!--</el-table-column>-->
            </el-table>
          </el-form>
          <div style="height: 40px">
          <el-button size="mini" style="float: right;margin: 10px;" type="primary" @click="save"></el-button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  export default{
    name:"SoldOut",
    mounted(){
      this.id = this.$route.params.id;
      this.getList();
    },
    data(){
      var _this = this;
      var checkAmount = (rule, value, callback) => {
        if(_this.isNull(value)){
          callback();
        }
        let isNum = _this.isNumber(value)
        if (!isNum) {
          callback(new Error(''));
        } else {
          if(value < 0 ){
            callback(new Error('0'));
          }
          callback();
        }
      };
      return{
        id:"",
        toSoldOut:false,
        stockOutDownShelfList:[],
        detailForm:{
          tableData:[]
        },
        rules:{
          amount:[
            { validator: checkAmount, trigger: 'change' }
          ]
        },
      }
    },
    methods:{
      getList(){
        this.$http.get("/ys-web-wms/putOutStorageSoldOut/getStockOutDownShelfList?id="+this.id).then(function (result) {
          if(result.data.state == 200){
            this.stockOutDownShelfList = result.data.data?result.data.data:[];
          }else{
            this.$notify.error({
              title:"",
              message:result.data.message
            })
          }
        })
      },
      getWaitList(){
        this.$http.get("/ys-web-wms/putOutStorageSoldOut/getStockOutWaitDownShelfList?id="+this.id).then(function (result) {
          if(result.data.state == 200){
            let list = result.data.data?result.data.data:[];
            if(list.length>0){
              for(var i=0;i<list.length;i++){
                list[i].stockId = "";//id
                list[i].actualDownAmount = "";//
                list[i].goodsAllocationCode = "";//
                list[i].barcodeAmount = "";//
                list[i].barcodeList = [];//list
              }
            }
            this.detailForm.tableData = list;
          }else{
            this.$notify.error({
              title:"",
              message:result.data.message
            })
          }
        })
      },
      goodsAllocationCodeChange(row){
        let list = row.stockDetailList;
        row.stockId = "";
        row.stockInDate = "";//
        row.barcodeAmount = "";//
        for(var i=0;i<list.length;i++){
          if(row.goodsAllocationCode == list[i].storagebinCode){
            row.barcodeList = list[i].barcodeList;//
          }
        }
      },
      //
      barcodeChange(row){
        let list = row.barcodeList;
        for(var i=0;i<list.length;i++){
          if(row.stockId == list[i].stockId){
            row.stockInDate = list[i].stockInDate;//
            row.barcodeAmount = list[i].stockAmount;//
            row.downPersonId = this.$store.state.user.id;//id
            row.downPersonName = this.$store.state.user.name;//name
            return;
          }
        }
      },
      soldOut(){
        this.getWaitList();
        this.toSoldOut = true;
      },
      save(){
        let list = this.detailForm.tableData;
        let submitList = [];
        for(var i=0;i<list.length;i++){
          if(this.isNull(list[i].actualDownAmount)){
            continue;
          }
          if(list[i].goodsAllocationCode == ""){
            this.$message({
              message:""+list[i].materialName+"",
              type:"error",
              showClose:true
            })
            return;
          }
          if(list[i].stockId == ""){
            this.$message({
              message:""+list[i].materialName+"",
              type:"error",
              showClose:true
            })
            return;
          }
          if(list[i].actualDownAmount > list[i].barcodeAmount){
            this.$message({
              message:""+list[i].materialName+""+list[i].barcodeAmount,
              type:"error",
              showClose:true
            })
            return;
          }
          submitList.push(list[i]);
        }
        if(submitList.length == 0){
          this.$message({
            message:"",
            type:"warning",
            showClose:true
          })
          return;
        }
        let dataStr = JSON.stringify(submitList);
        this.$http.post("/ys-web-wms/putOutStorageSoldOut/downShelf",{data:dataStr,id:this.id}).then(function (result) {
          if(result.data.state == 200){
            this.$message({
              message:"",
              type:"success",
              showClose:true
            })
            this.getList();
            let _this = this;
            this.getPutOutStorageStatus(this.id,function (res) {
              _this.$store.state.putOutStorageInfo.stockOutStatusKey = res;
            })
            this.toSoldOut = false;
          }else{
            this.$notify.error({
              title:"",
              message:result.data.message
            })
          }
        })

      }
    }
  }
</script>
<style>

</style>



