<template>
  <div>
    <div class="right-topTitle">

    </div>
    <div class="card-box right-content">
      <div class="right-content-body">
        <el-row style="padding: 5px;margin-top: 10px">
          <steps></steps>
        </el-row>
        <div style="padding: 5px;height: 200px">
          <span class="right-content-body-little-title">
            
          </span>
          <el-form ref="form" :model="detailForm" label-width="130px" size="mini" style="width: 100%"  :inline-message="true">
            <el-form-item label=":" class="right-content-body-tableForm">
              <span>{{detailForm.stockOutBillCode}}</span>
            </el-form-item>
            <el-form-item label=":" class="right-content-body-tableForm" v-if="detailForm.blueRedFlagKey == '2'">
              <span>{{detailForm.oldBlueStockOutCode}}</span>
            </el-form-item>
            <el-form-item label=":" class="right-content-body-tableForm" prop="warehouseId" :rules="{ required: true, message: '', trigger: 'change' }">
              <el-select clearable v-model="detailForm.warehouseId" filterable placeholder="" @change="warehouseChange" style="width: 100%">
                <el-option
                  v-for="item in warehouseList"
                  :key="item.id"
                  :label="item.name"
                  :value="item.id">
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item label=":" class="right-content-body-tableForm" prop="stockOutTypeKey" :rules="{ required: true, message: '', trigger: 'change' }">
              <el-select clearable v-model="detailForm.stockOutTypeKey" filterable placeholder="" size="mini" style="width: 100%">
                <el-option
                  v-for="item in stockOutType"
                  :key="item.code"
                  :label="item.name"
                  :value="item.code">
                </el-option>
              </el-select>
            </el-form-item>
            <!--<el-form-item label=":" class="right-content-body-tableForm">-->
            <!--<el-select clearable v-model="detailForm.orderState" filterable placeholder="" size="mini" style="width: 100%">-->
            <!--<el-option-->
            <!--v-for="item in testData"-->
            <!--:key="item.code"-->
            <!--:label="item.name"-->
            <!--:value="item.name">-->
            <!--</el-option>-->
            <!--</el-select>-->
            <!--</el-form-item>-->
            <el-form-item label=":" class="right-content-body-tableForm">
              <el-select clearable v-model="detailForm.pickingPersonId" filterable placeholder="" size="mini" @change="pickingPersonChange" style="width: 100%">
                <el-option
                  v-for="item in allPersons"
                  :key="item.id"
                  :label="item.name"
                  :value="item.id">
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item label=":" class="right-content-body-tableForm">
              <el-select clearable v-model="detailForm.pickingDeptId" filterable placeholder="" size="mini" @change="pickingDeptChange" style="width: 100%">
                <el-option
                  v-for="item in warehouseDeptList"
                  :key="item.id"
                  :label="item.name"
                  :value="item.id">
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item label=":" class="right-content-body-tableForm" style="">
              <span>{{detailForm.stockOutPersonName}}</span>
            </el-form-item>
            <el-form-item label=":" class="right-content-body-tableForm">
              <span>{{detailForm.createDate}}</span>
            </el-form-item>
            <el-form-item label=":" class="right-content-body-tableForm" style="width: 98.2%">
              <el-input size="mini" placeholder="" v-model="detailForm.remarks"></el-input>
            </el-form-item>
          </el-form>
        </div>
        <!---->
        <div style="padding: 5px;">
          <span style="font-style: normal;font-size: 12px;color: #2B7C9F;font-weight: bold;margin-left: 7px;">
              
          </span>
          <el-button size="mini" style="float: right;margin: 0px 7px 2px 0px" type="primary" @click="addDetail" v-if="detailForm.blueRedFlagKey == '1'"></el-button>
          <el-form :model="detailForm" ref="detailForm" :inline-message="true">
            <el-table :data="detailForm.detailList" border style="width:100%" class="operation-table">
              <el-table-column prop="date" label="" align="center" :show-overflow-tooltip="true">
                <template slot-scope="scope">
                  <el-form-item label="" :prop="'detailList.'+scope.$index+'.materialCategoryCode'" v-if="scope.row.state=='edit' && detailForm.blueRedFlagKey == '1'" :rules="{ required: true, message: '', trigger: 'change' },">
                    <el-select clearable size="mini" v-model="scope.row.materialCategoryCode" filterable placeholder="" @change="materialCategoryCodeChange(scope.row)" style="width: 100%">
                      <el-option
                        v-for="item in materialCategoryList"
                        :key="item.id"
                        :label="item.name"
                        :value="item.id">
                      </el-option>
                    </el-select>
                  </el-form-item>
                  <span v-else>{{scope.row.materialCategoryName}}</span>
                </template>
              </el-table-column>
              <el-table-column prop="date" label="" align="center" :show-overflow-tooltip="true">
                <template slot-scope="scope">
                  <el-form-item label="" :prop="'detailList.'+scope.$index+'.materialCode'" v-if="scope.row.state=='edit' && detailForm.blueRedFlagKey == '1'" :rules="{ required: true, message: '', trigger: 'change' },">
                    <el-select clearable size="mini" v-model="scope.row.materialCode" filterable placeholder="" @change="materialCodeChange(scope.row)" style="width: 100%">
                      <el-option
                        v-for="item in scope.row.materialNameCodeList"
                        :key="item.materialCode"
                        :label="item.materialName+'('+item.materialCode+')'"
                        :value="item.materialCode">
                      </el-option>
                    </el-select>
                  </el-form-item>
                  <span v-else>{{scope.row.materialName}}</span>
                </template>
              </el-table-column>
              <el-table-column prop="materialCode" label="" align="center" :show-overflow-tooltip="true">
              </el-table-column>
              <el-table-column prop="modelType" label="" align="center" :show-overflow-tooltip="true">
              </el-table-column>
              <el-table-column prop="specification" label="" align="center" :show-overflow-tooltip="true">
              </el-table-column>
              <el-table-column prop="oldStockOutAmount" label="" align="center" :show-overflow-tooltip="true" v-if="detailForm.blueRedFlagKey == '2'">
              </el-table-column>
              <el-table-column prop="date" :label="detailForm.blueRedFlagKey == '1'?'':''" align="center" :show-overflow-tooltip="true">
                <template slot-scope="scope">
                  <el-form-item label="" :prop="'detailList.'+scope.$index+'.stockOutAmount'" v-if="scope.row.state=='edit'" :rules="[{ required: true, message: '', trigger: 'change' },rules.amount]">
                    <el-input size="mini" v-model="scope.row.stockOutAmount"></el-input>
                  </el-form-item>
                  <span v-else>{{scope.row.stockOutAmount}}</span>
                </template>
              </el-table-column>
              <el-table-column prop="stockAmount" label=""  align="center" :show-overflow-tooltip="true">
              </el-table-column>
              <el-table-column prop="unitName" label="" align="center" :show-overflow-tooltip="true">
              </el-table-column>
              <el-table-column prop="date" label="" fixed="right" align="center" width="87">
                <template slot-scope="scope">
                  <div v-if="scope.row.state=='edit'">
                    <el-button type="text" size="mini" @click="savelDetail(scope.row,scope.$index,'detailForm')"></el-button>
                    <el-button type="text" size="mini" @click="delDetail(scope.row,scope.$index)"></el-button>
                  </div>
                  <div v-else>
                    <el-button type="text" size="mini" @click="editDetail(scope.row,scope.$index)"></el-button>
                    <el-button type="text" size="mini" @click="delDetail(scope.row,scope.$index)"></el-button>
                  </div>
                </template>
              </el-table-column>
            </el-table>
          </el-form>
        </div>
        <div style="text-align: right" class="right-content-body-foot">
          <el-button size="mini" @click="submit('save')"></el-button>
          <el-button size="mini" type="primary" @click="submit('submit')"></el-button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import Steps from "../outBoundOrderInfo/Steps.vue"
  export default{
    name:"OutBoundOrderEdit",
    mounted:function () {
      this.id = this.$route.params.id
      this.getCopyInfo();
    },
    data(){
      var _this = this;
      var checkAmount = (rule, value, callback) => {
        if(_this.isNull(value)){
          callback();
        }
        let isNum = _this.isNumber(value)
        if (!isNum) {
          callback(new Error(''));
        } else {
          if(value < 0 ){
            callback(new Error('0'));
          }
          callback();
        }
      };
      return{
        id:"",
        index:"",
        materialCategoryList:[],//
        detailForm:{
          stockOutBillCode:"",//
          blueRedFlagKey:"1",//
          oldBlueStockOutCodeId:"",//id
          oldBlueStockOutCode:"",//
          stockOutTypeKey:"",//
          warehouseName:"",//
          warehouseId:"",//id
          pickingPersonName:"",//
          pickingPersonId:"",//Id
          pickingDeptName:"",//
          pickingDeptId:"",//id
          remarks:"",//
          stockOutPersonId:this.$store.state.user.id,//id
          stockOutPersonName:this.$store.state.user.name,//
          detailList:[]
        },
        rules:{
          date: [
            { required: true, message: '', trigger: 'change' },
            { min: 3, max: 5, message: ' 3  5 ', trigger: 'change' }
          ],
          amount:[
            { validator: checkAmount, trigger: 'change' }
          ]
        }
      }
    },
    methods:{
      getCopyInfo(){
        let _this = this;
        _this.$http.get("/ys-web-wms/putOutStorageInfo/getInfo?id="+this.id).then(function (result) {
          if(result.data.state == 200){
            let data = result.data.data;
            if(data){
              let info = {
                stockOutBillCode:"",//
                blueRedFlagKey:"1",//
                oldBlueStockOutCodeId:"",//id
                oldBlueStockOutCode:"",//
                stockOutTypeKey:"",//
                warehouseName:"",//
                warehouseId:"",//id
                pickingPersonName:"",//
                pickingPersonId:"",//Id
                pickingDeptName:"",//
                pickingDeptId:"",//id
                remarks:"",//
                stockOutPersonId:_this.$store.state.user.id,//id
                stockOutPersonName:_this.$store.state.user.name,//
              }
              let basicInfo = data.basicInfo?data.basicInfo:info;
              basicInfo.blueRedFlagKey = basicInfo.blueRedFlagKey + "";
              let detailList = data.detailList?data.detailList:[];
              if(detailList.length>0){
                for(var i=0;i<detailList.length;i++){
                  detailList[i].state = "nomal";
                  detailList[i].stockOutAmount = detailList[i].stockOutAmount + "";
                  detailList[i].unitName = detailList[i].measureUnit;//
                  detailList[i].materialNameCodeList=[];//+
                  detailList[i].firstEdit = true;
                }
              }
              basicInfo.detailList = detailList;
              _this.detailForm = Object.assign({},basicInfo);
            }else{
              _this.detailForm = {
                blueRedFlagKey:"1",//
                oldBlueStockOutCodeId:"",//id
                oldBlueStockOutCode:"",//
                stockOutTypeKey:"",//
                warehouseName:"",//
                warehouseId:"",//id
                pickingPersonName:"",//
                pickingPersonId:"",//Id
                pickingDeptName:"",//
                pickingDeptId:"",//id
                remarks:"",//
                stockOutPersonId:this.$store.state.user.id,//id
                stockOutPersonName:this.$store.state.user.name,//
                detailList:[]
              };
            }
          }else{
            _this.$notify.error({
              title:"",
              message:result.data.message
            })
          }
        })

      },
      submit(type){
        let submitData = Object.assign({},this.detailForm);
        if(submitData.detailList.length>0){
          for(var i=0;i<submitData.detailList.length;i++){
            submitData.detailList[i].materialNameCodeList = "";
            if(submitData.detailList[i].stockOutAmount>submitData.detailList[i].stockAmount){
              this.$message({
                message:""+submitData.detailList[i].materialCode + ""+submitData.detailList[i].stockAmount,
                type:"error",
                showClose:true
              })
              return;
            }
          }
        }else{
          this.$message({
            message:"",
            type:"error",
            showClose:true
          })
          return;
        }
        let errMsg = type=='save'?'':'';
        let dataStr = JSON.stringify(submitData);
        this.$http.post("/ys-web-wms/putOutStorageInfo/copy",{data:dataStr,type:type,id:this.id}).then(function (result) {
          if(result.data.state == 200){
            this.$message({
              message:errMsg + "",
              type:"success",
              showClose:true
            })
            this.$store.state.doAjaxSuccess = this.$store.state.doAjaxSuccess?false:true;
            this.$router.push("/putOutStorage")
          }else{
            this.$notify.error({
              title:errMsg + "",
              message:result.data.message
            })
          }
        })
      },
      //
      warehouseChange(val){
        let _this = this;
        if(this.detailForm.blueRedFlagKey == '1'){
          this.detailForm.detailList = [];
        }
        this.getCategoryList(val,function (res) {
          _this.materialCategoryList = res;
        });
        this.getNameById(val,this.warehouseList,function (res) {
          _this.detailForm.warehouseName = res;
        });
      },
      //
      pickingPersonChange(val){
        let _this = this;
        this.getNameById(val,this.allPersons,function (res) {
          _this.detailForm.pickingPersonName = res;
        });
      },
      //
      pickingDeptChange(val){
        let _this = this;
        this.getNameById(val,this.warehouseDeptList,function (res) {
          _this.detailForm.pickingDeptName = res;
        });
      },
      //
      materialCategoryCodeChange(row){
        let _this = this;
        row.materialCode = "";
        row.materialName = "";
        row.modelType = "";
        row.specification = "";
        row.unitName = "";
        row.materialNameCodeList=[];//
        this.getNameById(row.materialCategoryCode,this.materialCategoryList,function (result) {
          row.materialCategoryName = result;
          _this.getStockMaterialList(_this.detailForm.warehouseId,row.materialCategoryCode,function (res) {
            row.materialNameCodeList = res?res:[];
          })
        });
      },
      //
      materialCodeChange(row){
        let list = row.materialNameCodeList?row.materialNameCodeList:[];
        for(var i=0;i<list.length;i++){
          if(list[i].materialCode == row.materialCode){
            row.materialName = list[i].materialName;
            row.modelType = list[i].modelType;
            row.specification = list[i].specification;
            row.unitName = list[i].unitName;
            this.getWarehouseStockAmount(this.detailForm.warehouseId,row.materialCode,function (res) {
              row.stockAmount = res;
            })
            return;
          }
        }
      },
      editDetail(row,index){
        if(row.firstEdit){
          this.getMaterialInfoList(row.materialCategoryCode,function (res) {
            row.materialNameCodeList = res?res:[];
            row.state = "edit";
            row.firstEdit = false;
          })
        }else{
          row.state = "edit";
        }
      },
      delDetail(data,index){
        this.detailForm.detailList.splice(index, 1);
      },
      savelDetail(data,index,formName){
        if(this.checkForm(formName)){
          data.state = "nomal"
        }
      },
      addDetail() {
        if(this.checkForm('form')){
          let add = {
            stockOutAmount:"",//
            stockAmount:"",//
            materialCode:"",//
            materialName:"",//
            materialCategoryCode:"",//
            materialCategoryName:"",//
            modelType:"",//
            specification:"",//
            unitName:"",//
            materialNameCodeList:[],//+
            state:"edit"
          }
          this.detailForm.detailList.push(add)
        }else{
          this.$message({
            message:"",
            type:"warning",
            showClose:true
          })
        }
      }
    },
    components:{
        "steps":Steps
    },
    computed:{
      "warehouseList":function () {//
        return this.$store.state.selectList.warehouseList;
      },
      "stockOutType":function () {//
        return this.$store.state.enumList.stockOutType;
      },
      "allPersons":function () {//
        return this.$store.state.selectList.allPersons;
      },
      "warehouseDeptList":function () {//
        return this.$store.state.selectList.warehouseDeptList;
      },
    }
  }
</script>
<style>

</style>



