<template>
  <div id="put-out-info">
    <div class="right-topTitle-two">
      {{infoData.stockOutBillCode}}
    </div>
    <div style="margin-top: -20px">
      <span style="margin-left: 77px;color: #333333;font-size: 14px">{{ infoData.blueRedFlagKey|enumtext(blueRedFlag) }}</span>
    </div>
    <el-form label-width="130px" size="mini" style="width: 100%">
      <el-form-item label=":" class="right-content-body-tableForm">
        {{infoData.stockOutBillCode}}
      </el-form-item>
      <el-form-item label=":" class="right-content-body-tableForm">
        {{infoData.warehouseName}}
      </el-form-item>
      <el-form-item label=":" class="right-content-body-tableForm">
        {{infoData.stockOutTypeKey|enumtext(stockOutType)}}
      </el-form-item>
      <!--<el-form-item label=":" class="right-content-body-tableForm">-->
        <!--{{infoData.stockOutBillCode}}-->
      <!--</el-form-item>-->
      <el-form-item label=":" class="right-content-body-tableForm">
        {{infoData.pickingPersonName}}
      </el-form-item>
      <el-form-item label=":" class="right-content-body-tableForm">
        {{infoData.pickingDeptName}}
      </el-form-item>
      <el-form-item label=":" class="right-content-body-tableForm">
        {{infoData.createDate}}
      </el-form-item>
      <el-form-item label=":" class="right-content-body-tableForm">
        {{infoData.stockOutPersonName}}
      </el-form-item>
      <el-form-item label=":" class="right-content-body-tableForm" v-if="infoData.cancelTime">
        {{infoData.cancelTime}}
      </el-form-item>
      <el-form-item label=":" class="right-content-body-tableForm" v-if="infoData.cancelTime">
        {{infoData.cancelPersonName}}
      </el-form-item>
      <el-form-item label=":" class="right-content-body-tableForm" style="width: 98.2%" v-if="infoData.cancelTime">
        <el-tooltip class="item" effect="dark" :content="infoData.cancelReason" placement="top-start">
          <span>{{infoData.cancelReason}}</span>
        </el-tooltip>
      </el-form-item>
      <el-form-item label=":" class="right-content-body-tableForm" style="width: 98.2%">
        <el-tooltip class="item" effect="dark" :content="infoData.remarks" placement="top-start">
          <span>{{infoData.remarks}}</span>
        </el-tooltip>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
  export default{
    name:"Info",
    mounted(){
      this.id = this.$route.params.id;
      this.getInfo();
    },
    data(){
      return{
        id:"",
        infoData:{}
      }
    },
    methods:{
      getInfo(){
        this.$http.get("/ys-web-wms/putOutStorageInfo/getStockOutBasicInfo?id="+this.id).then(function (result) {
          if(result.data.state == 200){
            this.infoData = result.data.data?result.data.data:{};
          }else{
            this.$notify.error({
              title:"",
              message:result.data.message
            })
            this.infoData = {};
          }
          let dom = document.getElementById("put-out-info");
          if(this.infoData.cancelTime){
            dom.style.height = "267px";
          }else{
            dom.style.height = "215px";
          }
        });
      }
    },
    computed:{
      "blueRedFlag":function () {
        return this.$store.state.enumList.blueRedFlag;
      },
      "stockOutType":function () {//
        return this.$store.state.enumList.stockOutType;
      },
      "blueRedFlag":function () {//
        return this.$store.state.enumList.blueRedFlag;
      },
    },
    watch:{
      "$route.params.id":function () {
        this.id = this.$route.params.id;
        this.getInfo();
      }
    }
  }
</script>
<style>

</style>



